	module.exports = {"data":{"nodePost":{"title":"Looking Forward to DrupalCon Nashville","body":{"value":"<p>Very much looking forward to spending next week in Nashville attending my 4th <a href=\"https://events.drupal.org/nashville2018\">DrupalCon</a>. The spontaneous and unplanned stuff is half of the fun, but there are a few things that are locked on my schedule.</p>\r\n<p><b>Monday: <a href=\"https://events.drupal.org/nashville2018/decoupled-summit\">Decoupled Summit</a></b> - I'm excited to hear more about what people are doing in the decoupled Drupal space. This will be my first time attending a summit at DrupalCon as well.</p>\r\n<p><b>Tuesday 1 PM: <a href=\"https://events.drupal.org/nashville2018/bofs/components-and-layout-initiative\">Components and the Layout Initiative BOF</a></b> - I'll be leading an open discussion on how the Layout Initiative might impact our component-based workflows. I've really enjoyed the BOFs I've led in the past and expect this to be no exception.</p>\r\n<p><b>Wednesday 3:45 PM: <a href=\"https://events.drupal.org/nashville2018/sessions/hot-jamstack-building-music-discovery-app-drupal-and-react\">Hot JAMS(tack): Building a Music Discovery App with Drupal and React</a></b> - I'll be sharing my experience building a music discovery app with Drupal and React in my first ever talk at DrupalCon.</p>\r\n<p><b>Thursday: Who knows!</b></p>\r\n<p><b>Friday: <a href=\"https://events.drupal.org/nashville2018/sprints\">Sprints</a></b> - after a week of exciting new ideas, having a sprint to go hands on is a great way to end the week.  I plan on getting back to some forward progress on the <a href=\"https://github.com/backlineint/foundation_patterns\">Foundation Patterns</a> theme.</p>\r\n<p>Can't wait to say see friends old and new, learn a bunch of cool new stuff, and land a country music recording contract.</p>"},"relationships":{"image":null},"created":1523046899}},"pathContext":{"slug":"/post/looking-forward-to-drupalcon-nashville/"}}

